.movie-wrapper {
  width: 100%;

  display: flex;
  flex-flow: row wrap;
  justify-content: center;
}

.card {
  width: 18rem;
  height: 26rem;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  position: relative;

  color: $black;

  background-size: cover;
  background-position: center;
  
  h2 {
    color: $black;
    font-size: 1.25rem;
  }

  img {
    width: 15.6rem;
    min-height: 21.6rem;
  height: auto;
  }

  &:hover, &:focus {
    .details { 
      display: block;
    }
  }
}

.date {
  margin-bottom: 0.25rem;
  margin-top: 0;
 }

  .overview{
    max-height: 100%;

    margin: auto 0;
    font-size: 0.9rem;
    line-height: 1;
    text-align: left;
    text-overflow: ellipsis;
  }

  .read-more {
    position: absolute;
    bottom: 0.5rem;
    right: 0.5rem;

    color: $black;
    font-size: 0.8rem;
    font-weight: 600;
  }

.details {
  width: 100%;
  height: 100%;

  display: none;


  padding: 0.75rem;

  position: absolute;
  bottom: 0;

  background-color: rgba($turquoise, 0.9);

  z-index: 1;

  h2 {
    margin-top: 0.3rem;
    margin-bottom: 0.3rem;
  }

  &:hover, &:focus {
    display: block;
  }
}

